  and (min-width: 600px)
  and (max-width: 720px)
  and (orientation : portrait){
  
  .project_container{
    margin: 2em 5.3vw;
  }

  
  .top_section{
    display: flex;
    flex-direction: column;
  }

  .project_container .selected, .slick-slide, .slick-slider{
    height: 45vw;
    width: 94vw;
    margin-bottom: 1em;
  }

  .blogMain{
    margin-top: 2em;
    margin-right: -2em;
  }

  .toolbar{
    width: 94vw;
  }

  .blogMain > .blog_content{
    width: 80%;
    font-size: 0.9rem;
  }
  .thumb_image{
    width: 30vw;
    height: 30vh;
  }

  .svg-inline--fa.fa-w-14, .thumbnail{
    float: right;
    margin-top: 10px;
    width: 1.3em;
    height: auto;
  }

  .thumbnail{
    margin-right: 2em;
    margin-top: 0.7em;
  }
  
}

