.gallery{
    position:fixed;
    top:0;
    left:0;
    bottom:0;
    right:0;
    height:100%;
    width:100%;
    background: rgba(255,255,255,.97);
    z-index: 1000;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

.gallery_container{
    overflow-y: hidden;
    overflow-x: scroll;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(auto-fill,30vw);
    margin: 3em 3em;
}

.thumb_image{
    box-shadow: none !important;
}


@media only screen 
    and (min-width: 900px){

        .gallery_container{
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(auto-fill,27.5vw);
