.profilePage{
    margin-left: 3.3vw;
    margin-right: 3.3vw;
    display: flex;
    flex-direction: column;
}

.section{
    width: 100%;
}

.profileImage::before{
    content: "";
}

.profileImage{
    display: grid;
    grid-auto-flow: column;
    justify-content: start;
}

